.hover_block.is-open {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
}

.hover_block {
  opacity: 1;
  max-width: 767px;
  width: 100%;
  z-index: 999;
  background-color: #fff;
  border-radius: 16px;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 20px;
  transition: background .3s, border .3s, border-radius .3s, box-shadow .3s;
  display: flex;
  position: absolute;
  top: 24%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 32px #2b34531a;
}

.hover_title {
  color: #2b3453;
  padding: 15px;
  font-family: Volkhov, sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.33em;
}

.hover_border {
  border-top: 1px solid #2b34531a;
  margin-bottom: 10px;
}

.hover_box {
  padding: 15px;
  display: flex;
}

.hover_box__site {
  cursor: pointer;
  align-items: center;
  padding: 10px;
  display: flex;
}

.hover_box__site:not(:last-child) {
  margin-right: 15px;
}

.hover_box__site:hover, .hover_box__site:focus {
  background-color: #eaebee;
  border-radius: 18px;
}

.hover_box__image {
  width: 68px;
  height: 68px;
  filter: drop-shadow(1px 0 #000) drop-shadow(0 1px #000) drop-shadow(-1px 0 #000) drop-shadow(0 -1px #000);
  margin-right: 15px;
}

.hover_box__text {
